<template lang="html">
  <el-dialog
    :visible.sync="dialogVisible"
    title=""
    width="768px"
  >
    <el-form :model="formData" :rules="rules" label-width="80px" size="small" ref="form" class="clearfix">
      <el-form-item prop="number1" label="">
        <el-input v-model.trim="formData.number1" />
      </el-form-item>

      <el-form-item prop="number2" label="">
        <el-input v-model.trim="formData.number2" />
      </el-form-item>

      <el-form-item prop="number3" label="">
        <el-input v-model.trim="formData.number3" />
      </el-form-item>

      <el-form-item prop="number4" label="">
        <el-input v-model.trim="formData.number4" />
      </el-form-item>

      <el-form-item prop="number5" label="">
        <el-input v-model.trim="formData.number5" />
      </el-form-item>

      <el-form-item prop="number6" label="">
        <el-input v-model.trim="formData.number6" />
      </el-form-item>

      <el-form-item prop="number7" label="">
        <el-input v-model.trim="formData.number7" />
      </el-form-item>

      <el-form-item style="text-align: right">
        <el-button @click="handleConfirm" type="primary" size="small"></el-button>
      </el-form-item>
    </el-form>
  </el-dialog>
</template>

<script>
import config from '@/config/data'

import validators from '@/config/form'

export default {
  name: 'LotteryResultsDialogCancel',
  props: {
    data: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      formData: {},
      rules: {
        number1: validators.validateRequired(''),
        number2: validators.validateRequired(''),
        number3: validators.validateRequired(''),
        number4: validators.validateRequired(''),
        number5: validators.validateRequired(''),
        number6: validators.validateRequired(''),
        number7: validators.validateRequired('')
      },
      results: [],
      dialogVisible: false
    }
  },
  // 
  watch: {
    data () {
      // 
      this.balls = Object.assign([], this.data.balls)
      this.results = []
    }
  },
  methods: {
    // 
    choseBall (number, index) {
      if (this.results.length < this.data.ballNum) {
        this.results.push(number)

        // 
        if (!this.data.repeat) this.balls.splice(index, 1)
      } else {
        this.$message.warning(` ${this.data.ballNum} `)
      }
    },
    // 
    undoBall (number, index) {
      this.results.splice(index, 1)
      // 
      if (!this.data.repeat) {
        this.balls.push(number)
        this.balls.sort((a, b) => a - b)
      }
    },
    // 
    handleConfirm () {
      this.$message.warning('...')
      return
      // eslint-disable-next-line
      console.log(this.data)
      console.log(this.formData)
      this.$refs.form.validate((valid) => {
        if (valid) {
          this.$httpAPI.updateSixResultManual(this.formData).then(response => {
            if (response.data.data) {
              this.$emit('on-updated')

              this.$message.success(config.UPDATE_SUCCEEDED)
            } else {
              this.$message.error(`${config.UPDATE_FAILED}: ${response.data.msg}`)
            }
          }).catch(error => console.log(error))
        } else {
          this.$message.warning(config.VALIDATION_FAILED)
        }
      })
    },
    // 
    toggleDialogVisible (status) {
      this.dialogVisible = status
    }
  }
}
</script>

